<template>
  <div class="">
    <!-- <p class="common-breadcrumb"></p> -->
    <el-form
      :inline="true"
      size="medium"
      class="marginTop20 common-form-inline"
    >
      <el-form-item label="">
        <el-input v-model="visitorName" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="handleSearchByName" style="width: 100px;"></el-button>
        <el-button type="primary" @click="handleAddVisitor"></el-button>
        <!--<el-button type="primary" @click="handleDownSubPhoto"></el-button>-->
        <!--<el-button type="primary" @click="handleSendMessages"></el-button>-->
      </el-form-item>
      <div v-show="false">
        <canvas  style="width:300px;height:300px" id="canvas" ref="canvas"></canvas>
      </div>
    </el-form>
    <!---->
    <div class="common-table">
      <el-table header-row-class-name="table-header"  border  style="width: 100%" @selection-change="handleSelectionChange"
        :data="tableData">

        <!--<el-table-column type="selection" label="" width="50"></el-table-column>-->
        <el-table-column type="index" label="" width="50" align="left" header-align="left"></el-table-column>
        <el-table-column prop="planBeginTime" label="" width="200" align="left" header-align="left">  </el-table-column>
        <el-table-column prop="visitingTime" label=""  align="left" header-align="left">  </el-table-column>
        <el-table-column prop="vistorNum" label=""  align="left" header-align="left">  </el-table-column>
        <el-table-column prop="isVip" label=""  align="left" header-align="left">  </el-table-column>
        <el-table-column prop="isCar" label=""  align="left"> </el-table-column>
        <el-table-column prop="carNum" label=""  align="left" header-align="left"> </el-table-column>
        <el-table-column prop="visitorStatus" label=""  align="left"> </el-table-column>
        <el-table-column prop="submitStatus" label=""  align="left"> </el-table-column>
        <!--<el-table-column prop="auditingType" label="" width="110" align="center"> </el-table-column>-->
       <!-- <el-table-column prop="recordType" label="" width="95" align="center"> </el-table-column>-->
        <el-table-column prop="userName" label="" align="left" header-align="left"> </el-table-column>
        <!--<el-table-column prop="reviewNumAndName" label="" align="left" header-align="left"> </el-table-column>-->
        <!--<el-table-column prop="reviewTime" label="" width="160" align="left" header-align="left"> </el-table-column>-->
        <el-table-column prop="downStatus" label="" align="left" header-align="left"> </el-table-column>
        <el-table-column label="" width="90" align="left" header-align="left">
          <template slot-scope="scope">
            <el-button size="mini" type="text"
              @click="handleLookInfo(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
        <el-table-column label="" width="140" align="left" header-align="left">
          <template slot-scope="scope">
            <!--<el-button size="mini" type="text"
                        v-show="scope.row.submitStatus===''"
                       @click="handleConfirmVisit(scope.$index, scope.row)"> </el-button>
            <el-button size="mini" type="text"
                       v-show="scope.row.submitStatus===''"
                       @click="handleInvalidVisit(scope.$index, scope.row)"></el-button>-->
            <el-button size="mini" type="text"
                        v-show="scope.row.submitStatus==='' && scope.row.downStatus===''"
                       @click="handleInvalid(scope.$index, scope.row)"> </el-button>
            <!--<el-button size="mini" type="text"
                        v-show="scope.row.submitStatus!=='' && scope.row.downStatus!==''"
                       @click="handleDownSubPhoto(scope.$index, scope.row)" style="margin-left: 0px"> </el-button>-->
            <el-button size="mini" type="text"
                        v-show="scope.row.submitStatus==='' && scope.row.visitorStatus===''"
                       @click="handleEnd(scope.$index, scope.row)"> </el-button>

          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="marginTop10 text-right">
      <el-pagination
        background
        :current-page="currentPage"
        :page-sizes="[10, 20, 30]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      >
      </el-pagination>
    </div>
    <!--  -->
    <AddVisitorDialog :visible="dialogVisible" @confirmadddialog="handleConfirmDialog" @cancleadddialog="handleCancleDialog"></AddVisitorDialog>
    <!--  -->
    <div class="common_lookInfoDialog">
      <el-dialog title=""
        :visible.sync="visitorInfomation"
        :close-on-click-modal="false"
        class="edit-form"
        width="1000px"
        >
        <el-form :v-model="editForm" label-width="110px" ref="editForm" :inline="true">
          <div class="">
            <el-form-item label="">
              <el-input  v-model="editForm.sanyBussVisitor.planBeginTime" disabled></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="editForm.sanyBussVisitor.visitingTime" disabled></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="editForm.sanyBussVisitor.vistorNum" disabled></el-input>
            </el-form-item>
            <!--<el-form-item label="">
              <el-input  v-model="editForm.sanyBussVisitor.beginTime" disabled></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="editForm.sanyBussVisitor.endTime" disabled></el-input>
            </el-form-item>-->
            <el-form-item label="">
              <el-input v-model="editForm.sanyBussVisitor.isCar" disabled></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input  v-model="editForm.sanyBussVisitor.carNum" disabled></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input  v-model="editFormFirstIsVip" disabled></el-input>
            </el-form-item>
            <br />
            <el-form-item label="" >
              <el-input disabled style="width:795px;"
                        type="textarea" :rows="3"
                        v-model="editForm.sanyBussVisitor.reason">
              </el-input>
            </el-form-item>
          </div>
        </el-form>
        <div class="common-table">
          <el-table header-row-class-name="table-header" border
                    :data="editForm.sanyBussVisitorDetailsList">
            <el-table-column type="index" label="" width="50"></el-table-column>
            <el-table-column prop="visitorName" label="" width="100" >  </el-table-column>
            <el-table-column prop="phone" label="" >  </el-table-column>
            <el-table-column prop="visitorId" label="" >  </el-table-column>
            <el-table-column prop="carNo" label=""></el-table-column>
           <!-- <el-table-column prop="beginTime" label=""  >  </el-table-column>
            <el-table-column prop="endTime" label="" >  </el-table-column>-->
            <el-table-column prop="isSub" label="" width="100" >  </el-table-column>
            <el-table-column label="" >

              <template slot-scope="scope">
                <el-upload
                  class="upload-demo"
                  action="string"
                  accept="image/jpeg,image/png,image/jpg"
                  list-type="picture"
                  :show-file-list="false"
                  :before-upload="onBeforeUploadImage"
                  :http-request="UploadImage"
                >
                <el-button size="mini" type="primary"
                           v-show="isShowDownSubBtn"
                           @click="handleRepeatUploadPhoto(scope.$index, scope.row)"> </el-button>&nbsp;
                  <el-tooltip  v-show="isShowDownSubBtn" content="jpg/png2M" placement="top">
                    <img src="./images/imageSize.png" style="vertical-align: middle" alt="">
                  </el-tooltip>
                </el-upload>

              </template>
            </el-table-column>
            <el-table-column prop="imgUrl" label="" width="100">
              <template slot-scope="scope">
                <el-button size="mini" type="text" v-show="scope.row.imgUrl"
                            @click="checkPicture(scope.$index, scope.row)"></el-button>
              </template>
            </el-table-column>
            <el-table-column label="" width="100">
              <template slot-scope="scope">
                <el-button size="mini" type="text"
                            @click="handleSendMessagesInformation(scope.$index, scope.row)"></el-button>
              </template>
            </el-table-column>

          </el-table>
        </div><br/>
        <span slot="footer" class="dialog-footer">
      <!--<el-button v-show="isShowDownSubBtn" type="primary" @click="handleImageDownSub(editForm)"> </el-button>-->
    </span>
        <!--<div class="common-table">
          <el-table header-row-class-name="table-header"  border
                    :data="editForm.sanyBussVisitorCarList">
            <el-table-column type="index" label="" width="100"></el-table-column>
            <el-table-column prop="carNo" label=""></el-table-column>
            <el-table-column prop="beginTime" label=""></el-table-column>
            <el-table-column prop="endTime" label=""></el-table-column>
          </el-table>
        </div>-->
      </el-dialog>
    </div>
    <!--  -->
    <!--  -->
    <div class="checkPictureInformation">
      <el-dialog title=""
        :visible.sync="checkPictureInformationVisible"
        :close-on-click-modal="false"
        class="edit-form">
        <div style="display: flex;justify-content: center;align-items: center;overflow: hidden;">
            <img :src="currentImg" alt="" style="max-width:100%;">
        </div>
      </el-dialog>
    </div>
    <!-- end -->
  </div>
</template>

<script>
  import {
    getVisitorAllByVisitorNameRequest,
    confirmVisitRequest,
    invalidVisitRequest,
    cancelRequest,
    manualEndRequest,
    getVisitorDetailsRequest,
    fileUploadRequest,

  } from '../../../api/businessManageApi'
import {
  reqSendMessages,reqSubPhotoes,reqRUploadImage,reqSendMessageSingle,
}from '../../../api'

  // import {date}from '../../../util/dateFormat'
  import {date}from '../../../util/dateFormatEasy'
  import AddVisitorDialog from './addVisitorDialog'
  import QRCode from 'qrcode'

  import { mapState } from 'vuex';
  export default {
    name: "Template",
    components: {
      AddVisitorDialog
    },
    data() {
      return {
        tableData: [],
        editFormFirstIsVip:'',//---0422---
        qrCode:'',//
        reImageId:'',//--id
        currentPage: 1, // 
        pageSize: 10, // 
        totalNum: 0, // 
        visitorName: '', // 
        editForm: { // 
          sanyBussVisitor: {},
          sanyBussVisitorDetailsList: [
            {
              imgUrl:'',//
            }
          ],
          sanyBussVisitorCarList: [],
        },
        dialogVisible: false, // 
        visitorInfomation: false, // 
        checkPictureInformationVisible: false, // 
        currentImg: '',
        multipleSelection:[],//     
        currentUploadIndex:"",//
        isShowDownSubBtn:false,//  &&  
        // submitStatusComing:true,//true,false(,)
      }
    },
    mounted () {
      // 
      this.getAllVisitorData(this.currentPage,this.pageSize,this.visitorName)
    },
    methods: {
      async getAllVisitorData(pageNum, pageSize, visitorName) {
        let visitorNameQuery;
        if (visitorName === '') {
          visitorNameQuery=== null
        } else {
          visitorNameQuery = visitorName
        }
        const res = await getVisitorAllByVisitorNameRequest(pageNum, pageSize, visitorNameQuery);
        if (!res || !res.data || !res.data.code === '200') return;
        const {list, total} = res.data.data
        var qrCodeList = res.data.data.list
        this.totalNum = total
        this.tableData = list.map((item) => {
          var visitorStatus = item.visitorStatus // 
          if (visitorStatus === '01') {
            visitorStatus = ''
          } else if (visitorStatus === '02') {
            visitorStatus = ''
          } else if (visitorStatus === '03') {
            visitorStatus = ''
          } else if (visitorStatus === '04') {
            visitorStatus = ''
          }
          var submitStatus = item.submitStatus // 
          if (submitStatus === '01') {
            submitStatus = ''
          } else if (submitStatus === '02') {
            submitStatus = ''
          } else if (submitStatus === '03') {
            submitStatus = ''
          }
          var auditingType = item.auditingType // 
          if (auditingType === '01') {
            auditingType = ''
          } else if (auditingType === '02') {
            auditingType = ''
          }
          var recordType = item.recordType // 
          if (recordType === '01') {
            recordType = ''
          } else if (recordType === '02') {
            recordType = ''
          } else if (recordType === '03') {
            recordType = ''
          }
          var downStatus = item.isSub // 
          if (downStatus === '1') {
            downStatus = ''
          } else if (downStatus === '2') {
            downStatus = ''
          }
          var isCar = item.isCar // 
          if (isCar === '1') {
            isCar = ''
          } else if (isCar === '0') {
            isCar = ''
          }
          var isVip = item.isVip // 
          if (isVip === 1) {
            isVip = 'Vip'
          } else if (isVip === 0) {
            isVip = ''
          }
         /* else if(downStatus === '3'){
            downStatus = ''
          }else if(downStatus === '4'){
            downStatus = ''
          }*/
          var visitingTime = item.visitingTime
          console.log('item.visitingtime:',visitingTime)
          if(visitingTime === '01'){
            visitingTime = ''
          }else if(visitingTime === '02'){
            visitingTime = ''
          }else if(visitingTime === '03'){
            visitingTime = ''          }
          // /
          let auditingCode = item.auditingCode? item.auditingCode: ''
          let auditingName = item.auditingName? item.auditingName: ''
          let reviewNumAndName = ''
          if (auditingCode) {
            reviewNumAndName = auditingCode+'/'+auditingName
          } else {
            reviewNumAndName = auditingName
          }
          // this.qrCode = item.qrCode
          this.documentNo = item.documentNo
          return {
            id: item.id, // id
            planBeginTime:item.planBeginTime, // /*date(item.planBeginTime)*/
            // planEndTime: item.planEndTime, // 
            vistorNum: item.vistorNum, // 
            isCar: isCar, // 1.0.
            isVip: isVip, // 1.VIp0.
            carNum: item.carNum, // 
            visitorStatus: visitorStatus, // 01:0203'
            submitStatus: submitStatus, // 01:02
            auditingType: auditingType, // 0102'
            recordType: recordType, // 010203'
            visitingTime: visitingTime, // 
            // operatorNumAndName: item.operaterCode?item.operaterCode:'' + '/' + item.operaterName?item.operaterName:'', // /
            operatorNumAndName: item.operaterCode, // /
            reviewNumAndName: reviewNumAndName, // /
            reviewTime: item.auditingTime, // 
            downStatus: downStatus, // 
            qrCode:item.qrCode,  //
            phone:item.phone, // 
            documentNo:item.documentNo,//
            userName:item.userName,
          }
          console.log('isVip:',isVip)
        })
      },
      // 
      handleSizeChange(val) {
        // console.log(`: ${val}`);
        this.pageSize = val
        this.currentPage = 1;
        this.getAllVisitorData(this.currentPage, this.pageSize, this.visitorName)
      },
      // 
      handleCurrentChange(val) {
        // console.log(`: ${val}`);
        this.currentPage = val
        this.getAllVisitorData(this.currentPage, this.pageSize, this.visitorName)
      },
      // 
      async handleSearchByName() {
        this.currentPage = 1
        this.getAllVisitorData(this.currentPage,this.pageSize,this.visitorName);
      },

      // 
      handleLookInfo(index, row) {
        console.log('row:', row);
        this.visitorInfomation = true
        this.qrCode = row.qrCode
        //
        row.downStatus === '' ?  this.isShowDownSubBtn = false : this.isShowDownSubBtn = true
        // 
        this.getVisitorDetailsData(row.id)
      },
      async getVisitorDetailsData (visitorId) {
        const res = await getVisitorDetailsRequest(visitorId);
        // console.log('YYYYYYYY', res)
        if (!res || res.data.code!==200) {
          this.$message({
            type: 'error',
            message: res.data.msg
          });
          return
        }
        this.editForm = res.data.data
        this.editForm.sanyBussVisitor.isCar = this.editForm.sanyBussVisitor.isCar==='1'? '':''
        if(this.editForm.sanyBussVisitor.isVip===1){
          this.editForm.sanyBussVisitor.isVip = 'Vip'
        }else if(this.editForm.sanyBussVisitor.isVip===0){
          this.editForm.sanyBussVisitor.isVip = ''
        }
        if(this.editForm.sanyBussVisitor.visitingTime === '01'){
          this.editForm.sanyBussVisitor.visitingTime = ''
        }else if(this.editForm.sanyBussVisitor.visitingTime === '02'){
          this.editForm.sanyBussVisitor.visitingTime = ''
        }else if(this.editForm.sanyBussVisitor.visitingTime === '03'){
          this.editForm.sanyBussVisitor.visitingTime = ''
        }

        this.editForm.sanyBussVisitorDetailsList.map(item=>{
          // var isSub = item.isSub // 
          if (item.isSub === '1') {
            item.isSub = ''
          } else if (item.isSub === '2') {
            item.isSub = ''
          }
          if (item.isVip === 1) {
            this.editFormFirstIsVip = 'Vip'
          } else if (item.isVip === 0) {
            this.editFormFirstIsVip = ''
          }
          // console.log('isSub:',item.isSub)
          // console.log('sanyBussVisitorDetailsList:',this.editForm.sanyBussVisitorDetailsList)
          return {isSub:item.isSub}

        })

      },
      // 
      handleConfirmVisit(index, row) {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          // console.log('id',row.id)
          // console.log('id:',sessionStorage.getItem('userId'))
          this.confirmVisitData(row.id, 1)
        }).catch(() => {

        });
      },
      // 
      async confirmVisitData (visitorId, source) {
        const res = await confirmVisitRequest(visitorId, source);
        // console.log('',res)
        if (res.data.code === 200) {
          this.$message({
            type: 'success',
            message: res.data.data
          });
          // 
        } else {
          this.$message({
            type: 'success',
            message: res.data.msg
          });
        }
      },
      // //
      async InvalidCancelManualEndData (visitorId, requestName) {
        let requestNames = requestName
        const res = await requestNames(visitorId);
        // console.log('',res)
        if (res.data.code === 200) {
          this.$message({
            type: 'success',
            message: res.data.data
          });
          // 
          this.getAllVisitorData(this.currentPage, this.pageSize, this.visitorName)
        } else {
          this.$message({
            type: 'success',
            message: res.data.msg
          });
        }
      },
      // 
      handleInvalidVisit(index, row) {
        console.log(':',row)
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.InvalidCancelManualEndData(row.id, invalidVisitRequest)
        }).catch(() => {

        });
      },
      // 
      handleInvalid(index, row) {
        console.log(index, row);
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.InvalidCancelManualEndData(row.id, cancelRequest)
        }).catch(() => {

        });
      },
      // 
      handleEnd(index, row) {
        console.log(index, row);
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.InvalidCancelManualEndData(row.id, manualEndRequest)
        }).catch(() => {

        });
      },
      //
      handleSelectionChange(val) {
        this.multipleSelection = val;
        console.log('multipleSelection:',this.multipleSelection)
      },

    //
    handleDownSubPhoto(index,row){
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
          //
          var idItem= row.id
          this.subSendImage(idItem)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
      //
      async subSendImage(visitorId){
        const res = await reqSubPhotoes(visitorId)
        if(res&&res.data&&res.data.code===200){
          //
            this.$message({
              type:'success',
              message:res.data.data
            })
          //
          this.currentPage = 1
          this.getAllVisitorData(this.currentPage,this.pageSize,this.visitorName);
        }else{
          this.$message({
            type:'error',
            message:res.data.data
          })
        }
      },

    //---
    handleSendMessages(){
      if(this.multipleSelection.length === 0){
        this.$message({
          type:'error',
          message:''
        })
        return
      }
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        const {multipleSelection} = this
        var messageDto =[]
        var messageDtoqrCodeArr =[]
        console.log('multipleSelection:',multipleSelection)
        for (var i = 0; i < multipleSelection.length; i++) {
          var ItemObj = {}
          ItemObj.uid = multipleSelection[i].id
          // ItemObj.qrCode = `twodimension/${multipleSelection[i].qrCode}`
          // ItemObj.qrCode = `sanyvisitorcontrol/sanyvisitormanage/#/twodimension/${i}`
          ItemObj.qrCode = multipleSelection[i].qrCode
          ItemObj.verifCode = multipleSelection[i].documentNo
          messageDto.push(ItemObj)
          messageDtoqrCodeArr.push(multipleSelection[i].qrCode)
          //
        }
        this.sendMessageAsync(messageDto)
      /*    //
        var qrCodeArr = []
        messageDtoqrCodeArr.forEach((item,index) =>{
          var qrcodeItem = item
          console.log('qrcode:',qrcodeItem)
          //qrcode
          let canvasID = this.$refs.canvas
          QRCode.toCanvas(canvasID, qrcodeItem, function (error) {
            if (error) console.error(error)
          })
          var imgSrc = canvasID.toDataURL("image/png");
          qrCodeArr.push(imgSrc)
          console.log('',qrCodeArr)
          // this.$store.commit('getQrCodeImg',qrCodeArr)
          this.$route.params.id = index
        })
         localStorage.setItem('getQrCodeImg',JSON.stringify(qrCodeArr))*/

        // localStorage.setItem('getQrCodeImg',JSON.stringify(messageDtoqrCodeArr))
        // this.$store.commit('getQrCodeImg',messageDtoqrCodeArr)

      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    //---
      handleSendMessagesInformation(index,row){
        console.log('row0000000:',row)
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
       var messageDto = []
        var ItemObj = {}
        ItemObj.uid = row.id
        ItemObj.qrCode = row.qrCode
        ItemObj.verifCode = row.verifCode
        messageDto.push(ItemObj)
        this.sendMessageSingle(messageDto)
        //
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
      //0328---
      async sendMessageSingle(messageDto){
        const res = await reqSendMessageSingle(messageDto)
        if(res&&res.data&&res.data.code===200){
          this.$message({
            type:'success',
            message:''
          })
          //
          this.getAllVisitorData(this.currentPage,this.pageSize,this.visitorName)
        }else {
          this.$message({
            type:'error',
            message:res.data.msg
          })
        }

      },
      //
      async sendMessageAsync(messageDto){
        const res = await reqSendMessages(messageDto)
        if(res&&res.data&&res.data.code===200){
          this.$message({
            type:'success',
            message:''
          })
          //
          this.getAllVisitorData(this.currentPage,this.pageSize,this.visitorName)
        }else {
          this.$message({
            type:'error',
            message:res.data.msg
          })
        }
      },
      // 
      handleAddVisitor() {
        this.dialogVisible = true
      },
      // 
      handleConfirmDialog (val) {
        this.dialogVisible = val
        this.currentPage = 1
        this.getAllVisitorData(this.currentPage, this.pageSize, this.visitorName)
      },
      // 
      handleCancleDialog (val) {
        this.dialogVisible = val
      },
      // 
      checkPicture(index, row){
        this.checkPictureInformationVisible = true
        this.currentUploadIndex = index
        this.currentImg = this.editForm.sanyBussVisitorDetailsList[this.currentUploadIndex].imgUrl
        console.log('row',row)
      },
      //
      handleRepeatUploadPhoto(index,row){
        this.currentUploadIndex = index
        this.reImageId = row.id
       // console.log(':',row)
      },
      /*getUpload (index) {
        return `uploadPerson${index}`
      },*/
      onBeforeUploadImage(file) {
        const isIMAGE = file.type === 'image/jpeg' || 'image/jpg' || 'image/png'
        const isLt1M = file.size / 1024 / 1024 < 2
        if (!isIMAGE) {
          this.$message.error('!')
        }
        if (!isLt1M) {
          this.$message.error(' 1MB!')
        }
        return isIMAGE && isLt1M
      },
      async UploadImage(param){
        var uploadObj = `uploadPerson${this.currentUploadIndex}`
        var formData = new FormData(document.getElementById(`${uploadObj}`));
        formData.append('file', param.file)
        console.log('param:',param)
        const res = await reqRUploadImage(formData,this.reImageId)
        if(res&&res.status===200){
          this.$message({
            type:'success',
            message:''
          })
          let {code,data,msg} = res.data;
          console.log('imgUrl:',data)
          this.editForm.sanyBussVisitorDetailsList[this.currentUploadIndex].imgUrl = data
        }
      },
      //
      handleImageDownSub(editForm){
        this.visitorInfomation = false
        //
        var reLoadId =editForm.sanyBussVisitor.id
        this.subSendImage(reLoadId)
      },
    }
  }
</script>

