<template>
<div>
<el-dialog
  title=""
  :close-on-click-modal="false"
  :visible.sync="dialogVisibles"
  @close="handleDialogClose"
  width="1120px">
    <div class="addVisitorDialog">
      <!--  -->
      <el-form :model="formInline.sanyBussVisitor" ref="editDateForm" :rules="rules"
        :inline="true"
        size="medium"
        label-width="100px"
        class="marginTop20 common-form-inline addVisitorDialog_form"
        >
          <el-form-item label="" prop="planBeginTime">
            <el-date-picker
              v-model="formInline.sanyBussVisitor.planBeginTime"
              type="date"
              format="yyyy-MM-dd"
              value-format="yyyy-MM-dd"
              :default-value="new Date()"
              :picker-options="pickerOptionsStart"
              @change="selectDateValueControl"
              placeholder="">
            </el-date-picker>
          </el-form-item>
         <!-- <el-form-item label="" prop="planEndTime">
            <el-date-picker
              v-model="formInline.sanyBussVisitor.planEndTime"
              type="datetime"
              format="yyyy-MM-dd HH:mm:ss"
              value-format="yyyy-MM-dd HH:mm:ss"
              :default-valsue="new Date()"
              :picker-options="pickerOptionsEnd"
              placeholder="">
            </el-date-picker>
          </el-form-item>-->
        <el-form-item label="" prop="visitingTime">
          <el-select v-model="formInline.sanyBussVisitor.visitingTime" :disabled="isSelected" placeholder="">
            <el-option
              v-for="item in visitingTimeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
          <el-form-item label="">
            <el-input-number :controls="false" disabled v-model="formInline.sanyBussVisitor.vistorNum" @change="handleInputPersonNum" :min="formInline.sanyBussVisitor.vistorNum" @blur="blurVisitorNum()"></el-input-number>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="formInline.sanyBussVisitor.isCar"  placeholder="" @change="isCarNum">
              <el-option
                v-for="item in isDriveCarOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        <el-form-item label="">
          <el-select v-model="isVip" placeholder="" @change="isVipChange">
            <el-option
              v-for="item in isVipOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
          <!--<el-form-item label="" v-show="formInline.sanyBussVisitor.isCar==='1'">
            <el-input-number :controls="true" v-model="formInline.sanyBussVisitor.carNum" :min="1" @change="handleInputCarNum"></el-input-number>&lt;!&ndash;@change="handleInputCarNum"&ndash;&gt;
          </el-form-item>-->
          <el-form-item label="">
            <el-input
              style="width:905px;"
              type="textarea"
              :rows="4"
              placeholder=""
              v-model="formInline.sanyBussVisitor.reason">
            </el-input>
          </el-form-item>
        <div>
          <el-form-item>
            <el-button type="primary" style="width: 100px" @click="handleInputPersonNum" ></el-button> &nbsp;&nbsp;
            <el-tooltip content="" placement="top">
              <img src="../../../../src/assets/images/imageSize.png" style="vertical-align: middle" alt="">
            </el-tooltip>
          </el-form-item>
        </div>
      </el-form>
      <!--  end -->
      <!--  start -->
      <!-- v-if="formInline.sanyBussVisitorDetailsList.length>0" -->
      <div
        class="addVisitorDialog_table addVisitorDialog_personTable">
        <table>
          <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <!--<th></th>-->
            <!--<th></th>-->
            <th> &nbsp;&nbsp;&nbsp;

            </th>
            <th></th>
            <th></th>
          </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in formInline.sanyBussVisitorDetailsList" :key="index" >

              <td v-text="index+1">1</td>
              <td style="position: relative">
                <!--regIsName-->
                <el-input v-model="item.visitorName" :class="{regIsNull:item.visitorName===''&& isShowUserName ?true:false}" @focus="focusUserName(item.visitorName,index)"  placeholder="" @blur="blurUserName(item.visitorName,index)" @change="inputChangeName(item.visitorName)"></el-input>
                <span style="position:absolute;top: 22px;left: 0px;color: #f56c6c;">*</span>
                <!--isShowUserName-->
                <div v-if="isShowUserName" v-show="item.visitorName===''?true:false" style="color: #F56C6C;text-align: left;position: absolute;top: 60px;left: 8px;" ></div>
              </td>
              <td style="position: relative">
                <el-radio v-model="item.gender" label='1'></el-radio>
                <el-radio v-model="item.gender" label='2'></el-radio>
                <span style="position:absolute;top: 22px;left: 0px;color: #f56c6c;">*</span>
                <div v-if="isShowGender" v-show="item.gender===''?true:false" style="color: #F56C6C;text-align: left;position: absolute;top: 60px;left: 8px;" ></div>
              </td>
              <td style="position: relative">
                <!--regIsNull-->
                <el-input v-model="item.phone" :class="{regIsNull:item.phone==='' && isShowPhone ?true:false}" placeholder="" @blur="blurPhone(item.phone)" @change="regTel(item.phone)"></el-input>
                <span style="position:absolute;top: 22px;left: 0px;color: #f56c6c;">*</span>
                <!--isShowPhone-->
                <div v-if="isShowPhone" v-show="item.phone===''? true : false" style="color: #F56C6C;text-align: left;position: absolute;top: 60px;left: 8px;" ></div>
              </td>
              <td style="position: relative">
                <!--regIsIDCard-->
                <el-input v-model="item.visitorId" :class="{regIsNull:item.visitorId==='' && isShowIDCard?true:false}" placeholder="" @blur="blurIdCard(item.visitorId)" @change="regID(item.visitorId,index)"></el-input>
                <span style="position:absolute;top: 22px;left: 0px;color: #f56c6c;">*</span>
                <!--isShowIDCard-->
                <div v-if="isShowIDCard" v-show="item.visitorId===''?true:false" style="color: #F56C6C;text-align: left;position: absolute;top: 60px;left: 8px;" ></div>
              </td>
              <td style="position: relative">
                <!-- regIsNull:item.carNo===''&&isShowCarNo?true:false -->
                <el-input v-model="item.carNo" :disabled="formInline.sanyBussVisitor.isCar === '0' ? true : false"  :class="{regIsNull:item.carNo==='' && formInline.sanyBussVisitor.isCar === '1'?true:false}"  placeholder="" @blur="blurCarNo" @change="regCarNo(item.carNo,index)"></el-input>
                <div v-show="item.carNo==='' && formInline.sanyBussVisitor.isCar === '1' ? true : false" style="color: #F56C6C;text-align: left;position: absolute;top: 60px;left: 8px;" ></div>
              </td>
              <!--<td>
                <el-input v-model="item.countCard" placeholder=""></el-input>
              </td>-->
              <!--<td>-->
                <!--<el-input v-model="item.downStatus" placeholder=""></el-input>-->
              <!--</td>-->
              <td>
                <div @click="handleClickUpload(index)">
                  <el-upload
                    class="upload-demo"
                    :ref="getUpload(index)"
                    :id="getUpload(index)"
                    action="string"
                    accept="image/jpeg,image/png,image/jpg"
                    list-type="picture"
                    :show-file-list="false"
                    :before-upload="onBeforeUploadImage"
                    :http-request="UploadImage"
                    :file-list="item.fileList"
                    >
                      <div  v-show="!item.imgUrl"><el-button size="small" type="primary"></el-button>&nbsp;&nbsp;
                        <el-tooltip content="jpg/png2M" placement="top">
                          <img src="./images/imageSize.png" style="vertical-align: middle" alt="">
                        </el-tooltip>
                      </div>
                      <div v-show="item.imgUrl"><el-button size="small" type="primary" ></el-button>&nbsp;&nbsp;
                        <el-tooltip content="jpg/png2M" placement="top">
                          <img src="./images/imageSize.png" style="vertical-align: middle" alt="">
                        </el-tooltip></div>
                  </el-upload>
                </div>
              </td>
              <td>
                <el-button
                  size="mini"
                  type="text"
                  v-show="item.imgUrl"
                  @click="handleLookImg(item, index)"></el-button>
              </td>
              <td>
                <el-button type="danger" style="width: 80px;height:32px;" size="mini" :disabled="deleteDisabled" @click="deleteSingleLine(item,index)"></el-button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <!--  end -->
      <!--  start -->
      <!--<div class="addVisitorDialog_table addVisitorDialog_carTable" v-if="formInline.sanyBussVisitorCarList.length>0 && formInline.sanyBussVisitor.isCar==='1'">
        <table>
          <thead>
            <tr>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in formInline.sanyBussVisitorCarList" :key="index">
              <td v-text="index+1"></td>
              <td>
                <el-input v-model="item.carNo" placeholder=""></el-input>
              </td>
            </tr>
          </tbody>
        </table>
      </div>-->
      <!--  end -->
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button @click="handleDialogCancle"> </el-button>
      <el-button type="primary" @click="handleDialogConfirm"> </el-button>
    </span>
</el-dialog>
<!--  -->
      <el-dialog title=""
                 :visible.sync="dialogVisibleImg"
                 :close-on-click-modal="false"
                 class="edit-form">
          <div class="inputText" style="display: flex;justify-content: center;align-items: center;overflow: hidden;">
              <img :src="lookCurrentImgUrl" alt="" style="max-width:100%;">
          </div>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="dialogVisibleImg=false"></el-button>
        </div>
      </el-dialog>
      <!-- end -->
</div>
</template>
<script>
import {
  addApplyRequest,
  fileUploadRequest
} from '../../../api/businessManageApi'
import {reqAddVisitorSuccessReq,reqrRegIDCard} from '../../../api'
import {checkPhone,checkIDCard,checkCarCard} from "../../../util/regExp";

export default {
  name: 'AddVisitorDialog',
  props: ['visible'],
  watch: {
    visible (val) {
      this.dialogVisibles = val
    }
  },
  data () {
    return {
      isSelected:false,//0401
      deleteDisabled:false,//
      regIsNull:false,//Class
      regIsIDCard:false,//Class
      regIsName:false,//Class
      isShowUserName:false,////
      isShowPhone:false,////
      isShowIDCard:false,////
      isShowGender:false,////
      currentUploadIndex: 0, // index
      dialogVisibleImg: false, // 
      dialogVisibles: this.visible, // 
      isTelephonetrue:false,//
      isIDCardtrue:false,//
      isCar: '0', // 
      isDriveCarOptions: [{ // 
        value: '1',
        label: ''
      }, {
        value: '0',
        label: ''
      }],
      isVip:'',
      isVipOptions: [{ // VIP
        label: 'VIP',
        value: '1',
      }, {
        label: '',
        value: '0',
      }],
      visitingTimeOptions:[{
        label: '',
        value: '01',
      },{label: '',
        value: '02',},
        {label: '',
          value: '03',}],
      pickerOptionsStart: { // 
        disabledDate(time) {
          return time.getTime() < Date.now()- 3600*1000*24;
        }
      },
      pickerOptionsEnd:{ // 
        disabledDate:(time) =>{
          return time.getTime() < new Date(this.formInline.sanyBussVisitor.planBeginTime).getTime() || time.getTime() > new Date(this.formInline.sanyBussVisitor.planBeginTime).getTime()
        }
      },
      rules: {
          planBeginTime: [
            { required: true, message: '', trigger: 'blur' },
          ],
          /*planEndTime: [
            { required: true, message: '', trigger: 'blur' }
          ]
*/
      },
      formInline: {
        sanyBussVisitor: {
          planBeginTime: '', // 
          // planEndTime: '', // 
          visitingTime: '03', // 
          vistorNum: 1, // 
          isCar: '0', // 
          isVip:0,//VIP
          carNum: 0, // 
          reason: '', // 
        },
        sanyBussVisitorDetailsList: [
          {
            visitorName: '', // 
            gender:'1',// 1  2
            phone: '', // 
            visitorId: '', // 
            imgUrl: '', // 
            carNo:'',//
            countCard:'',//
          }
        ], // 
        sanyBussVisitorCarList: [], // 
      },
      lookCurrentImgUrl: '' // 
    }
  },
  mounted () {

  },
  methods: {
    handleClickUpload (index) {
      this.currentUploadIndex = index
    },
    getUpload (index) {
      return `uploadPerson${index}`
    },
    onBeforeUploadImage(file) {
      const isIMAGE = file.type === 'image/jpeg' || 'image/jpg' || 'image/png'
      const isLt1M = file.size / 1024 / 1024 < 2
      if (!isIMAGE) {
        this.$message.error('!')
      }
      if (!isLt1M) {
        this.$message.error(' 2MB!')
      }
      return isIMAGE && isLt1M
    },
    async UploadImage(param){
      var uploadObj = `uploadPerson${this.currentUploadIndex}`
      var formData = new FormData(document.getElementById(`${uploadObj}`));
      formData.append('file', param.file)
      const res = await fileUploadRequest(formData)
      if(res&&res.status===200){
        this.$message({
          type:'success',
          message:''
        })
        let {code,data,msg} = res.data;
        this.formInline.sanyBussVisitorDetailsList[this.currentUploadIndex].imgUrl = data
      }
    },
    // 
    handleLookImg (item, index) {
      this.dialogVisibleImg = true;
      this.currentUploadIndex = index;
      this.lookCurrentImgUrl = this.formInline.sanyBussVisitorDetailsList[this.currentUploadIndex].imgUrl
    },
    // ,
    handleInputPersonNum() {
      var detailListLength = this.formInline.sanyBussVisitorDetailsList.length
      this.formInline.sanyBussVisitor.vistorNum = detailListLength+1
      var detailListObj =
        {visitorName: '', // 
          phone: '', // 
          gender:'1',//
          visitorId: '', // 
          imgUrl: '' ,// 
          carNo: ''} // 
      this.formInline.sanyBussVisitorDetailsList.splice(detailListLength,0,detailListObj)
      this.formInline.sanyBussVisitorDetailsList.length<=1 ? this.deleteDisabled = true : this.deleteDisabled = false
    },
    blurVisitorNum(){
      this.formInline.sanyBussVisitor.vistorNum = this.formInline.sanyBussVisitorDetailsList.length
    },
    //VIP
    isVipChange(val){
      let obj = {};
      obj = this.isVipOptions.find((item)=>{
        return item.value === val;
      });
      this.isVip = obj.label
      this.formInline.sanyBussVisitor.isVip =Number(obj.value)
      console.log('this.formInline.sanyBussVisitor.isVip:',this.formInline.sanyBussVisitor.isVip)
    },
    //   1 0
    isCarNum(){
      if(this.formInline.sanyBussVisitor.isCar === '0'){
        this.formInline.sanyBussVisitor.carNum = 0
        //
        this.formInline.sanyBussVisitorDetailsList.forEach(item=>{
          item.carNo = ''
        })
      }else if(this.formInline.sanyBussVisitor.isCar === '1'){
        this.formInline.sanyBussVisitor.carNum = 1
      }
      console.log(this.formInline.sanyBussVisitor.carNum)
    },

    //,
     handleInputCarNum(){
      let {carNum,vistorNum} = this.formInline.sanyBussVisitor
       /*if(carNum > vistorNum){
         this.formInline.sanyBussVisitor.carNum = this.formInline.sanyBussVisitor.vistorNum-1
        this.$message({
          type:'error',
          message:''
        })
         return
       }*/
     },
    // ,
   /* handleInputCarNum() {
      let carNum = this.formInline.sanyBussVisitor.carNum
      this.formInline.sanyBussVisitorCarList = []
      if (carNum <= 0) {
        return;
      }
      for (var i = 0; i< carNum; i++) {
        this.formInline.sanyBussVisitorCarList.push(
          {
            carNo: '', // 
          }
        )
      }
    },*/
    // 
    handleDialogConfirm() {
      this.$refs.editDateForm.validate(async (valid) => {
        if (!valid) return;
          const {sanyBussVisitorDetailsList} = this.formInline
        for (var i = 0; i <  sanyBussVisitorDetailsList.length; i++) {
          if(sanyBussVisitorDetailsList[i].visitorName === ''){
            this.regIsName= true
            this.isShowUserName= true
            return
          }else if(sanyBussVisitorDetailsList[i].gender === ''){
            this.isShowGender= true
            return
          }else if(sanyBussVisitorDetailsList[i].phone === ''){
            this.regIsNull= true
            this.isShowPhone= true
            return
          }else if(sanyBussVisitorDetailsList[i].visitorId === ''){
            this.regIsIDCard= true
            this.isShowIDCard= true
            return
          }
        }
        if(this.isTelephonetrue){
            this.$message({type:'error',message:''})
            return
        }
        if(this.isIDCardtrue){
            this.$message({type:'error',message:''})
            return
        }
        if(this.checkCarCardBoolean){
          this.$message({type:'error',message:''})
          return
        }

        if(this.formInline.sanyBussVisitor.visitingTime === ''){
          this.formInline.sanyBussVisitor.visitingTime = '02'
        }else if(this.formInline.sanyBussVisitor.visitingTime === ''){
          this.formInline.sanyBussVisitor.visitingTime = '03'
        }
        this.dialogVisibles = false
        this.$emit('dialogvisible', this.dialogVisibles)
        // Source1[]
        const {planBeginTime,visitingTime,vistorNum,isCar,isVip,carNum,reason} = this.formInline.sanyBussVisitor
        const [{visitorName,gender,phone,visitorId,imgUrl,carNo,countCard}] = this.formInline.sanyBussVisitorDetailsList

        console.log('this.sanyBussVisitor:',this.formInline.sanyBussVisitor)
        console.log('this.sanyBussVisitorDetailsList:',this.formInline.sanyBussVisitorDetailsList)


        this.formInline.source = 1
        this.addApplyRequestData(
          planBeginTime,visitingTime,vistorNum,isCar,isVip,carNum,reason,
          visitorName,gender,phone,visitorId,imgUrl,carNo,countCard,
          this.formInline.source
        )
      });


    },
    // 
    async addApplyRequestData(
      planBeginTime,visitingTime,vistorNum,isCar,isVip,carNum,reason,
      visitorName,gender,phone,visitorId,imgUrl,carNo,countCard,
      source
    ) {
      const res = await addApplyRequest(
        planBeginTime,visitingTime,vistorNum,isCar,isVip,carNum,reason,
        visitorName,gender,phone,visitorId,imgUrl,carNo,countCard,
        source
      );
      // console.log('',res)
      if (!res || res.data.code!==200) {
        this.$message({
          type: 'error',
          message: res.data.msg
        });
        return;
      }
      /*this.$message({
        type: 'success',
        message: ''
      });*/
      //
      let messageInfo = res.data.data.messageDto
      let messageArr = []
      messageInfo.forEach(item=>{
        let messageObj = {}
        messageObj.qrCode= item.qrCode
        messageObj.telephone= item.telephone
        messageObj.uid= item.uid
        messageObj.verifCode= item.verifCode
        messageArr.push(messageObj)
      })
      console.log('messageArr:',messageArr)
       this.addVisitorSuccess(messageArr)

      // 
      this.formInline.sanyBussVisitor = {
        vistorNum: 1, // 
        isCar: '0', // 
        carNum: 0, // 
        reason: '', // 
        planBeginTime: '', // 
        // planEndTime: '', // 
        visitingTime: '03', // 
      }
      this.formInline.sanyBussVisitorDetailsList = [{visitorName: '', // 
        phone: '', // 
        visitorId: '', // 
        gender:'1',//
        imgUrl: '' ,// 
        carNo: ''}]
      this.formInline.sanyBussVisitorCarList = []
      this.dialogVisibles = false
      this.$emit('confirmadddialog', this.dialogVisibles)
    },
    //
    async addVisitorSuccess(messageArr){
        const res = await reqAddVisitorSuccessReq(messageArr)
        if(res&&res.data.code===200){
          // this.$message({type:'success',message:''})
        }
    },
    // 
    handleDialogCancle() {
      this.dialogVisibles = false
      // 
      this.formInline.sanyBussVisitor = {
        vistorNum: 1, // 
        isCar: '0', // 
        carNum: 0, // 
        reason: '', // 
        planBeginTime: '', // 
        // planEndTime: '', // 
        visitingTime: '03', // 
      }
      this.formInline.sanyBussVisitorDetailsList = [{visitorName: '', // 
        phone: '', // 
        visitorId: '', // 
        gender:'1',//
        imgUrl: '' ,// 
        carNo: ''}]
      this.formInline.sanyBussVisitorCarList = []
      this.dialogVisibles = false
      this.$emit('cancleadddialog', this.dialogVisibles)
    },
    // 
    handleDialogClose() {
      this.dialogVisibles = false
      // 
      this.formInline.sanyBussVisitor = {
        vistorNum: 1, // 
        isCar: '0', // 
        carNum: 0, // 
        reason: '', // 
        planBeginTime: '', // 
        // planEndTime: '', // 
        visitingTime: '03', // 
      }
      this.formInline.sanyBussVisitorDetailsList = [{
        visitorName: '', // 
        phone: '', // 
        visitorId: '', // 
        gender:'1',//
        imgUrl: '' ,// 
        carNo: ''}]
      this.formInline.sanyBussVisitorCarList = []
      this.dialogVisibles = false
      this.$emit('confirmadddialog', this.dialogVisibles)
    },
    regTel(val){
      this.isTelephonetrue = checkPhone(val,this)
      console.log('isTelephone:',this.isTelephonetrue)
      val===''? this.regIsNull= true: this.regIsNull= false
    },
    async regID(val,index){
      this.isIDCardtrue = checkIDCard(val,this)
      //0401cardID
      let cardIdArr = []
      this.formInline.sanyBussVisitorDetailsList.forEach(item=>{
        cardIdArr.push(item.visitorId)
      })
      cardIdArr.splice(index,1)
      let indexNum = cardIdArr.includes(val)
      // console.log('indexNum:',indexNum)
      // console.log('cardIdArr:',cardIdArr)
      if(indexNum===true){
        this.formInline.sanyBussVisitorDetailsList[index].visitorId = ''
        this.$message({type:'error',message:''})
      }

      const res = await reqrRegIDCard(val)
      if(res&&res.data.code!==200){
        this.$message({
          type:'error',
          message:res.data.msg
        })
        this.formInline.sanyBussVisitorDetailsList[index].visitorId = ''
      }
      val===''? this.regIsIDCard= true: this.regIsIDCard= false
    },
    inputChangeName(visitorName){
       visitorName===''? this.regIsName= true: this.regIsName= false
    },
    blurUserName(val,index){
      console.log('val:',val)
      console.log('index:',index)
      val===''? this.regIsName= true: this.regIsName= false
      val===''? this.isShowUserName= true: this.isShowUserName= false
    },
    blurPhone(val){
      val===''? this.regIsNull= true: this.regIsNull= false
      val===''? this.isShowPhone= true: this.isShowPhone= false
    },
    blurIdCard(val){
      val===''? this.regIsIDCard= true: this.regIsIDCard= false
      val===''? this.isShowIDCard= true: this.isShowIDCard= false
    },
    blurCarNo(val){
      // val = val.replace(/\s*/g,"")
      val===''? this.isShowCarNo= true: this.isShowCarNo= false
    },
    //
    regCarNo(val){
      this.checkCarCardBoolean = checkCarCard(val,this)
    },
    ///input
    focusUserName(val,index){
      // val = ''
      console.log('val0000000000000:',val)
    },
    //
    deleteSingleLine(item,index){
      if(this.formInline.sanyBussVisitorDetailsList.length<=1){
        this.deleteDisabled = true   //
        this.$message({message:','})
        return
      }
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '!'
        });
        this.formInline.sanyBussVisitorDetailsList.splice(index,1)
        this.formInline.sanyBussVisitor.vistorNum = this.formInline.sanyBussVisitorDetailsList.length
        // this.formInline.sanyBussVisitor.carNum = this.formInline.sanyBussVisitor.vistorNum
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });

    },
    //
    selectDateValueControl(val){
      let dateone = new Date(new Date(new Date().toDateString()).getTime()+12*60*60*1000).getTime() //12
      let datezear = new Date(new Date(new Date().toDateString()).getTime()+8*60*60*1000).getTime()    //8
      let selectTime = new Date(val).getTime()
      let nowDate = Date.now()
      if(nowDate>dateone){
        if(selectTime === datezear){
          this.formInline.sanyBussVisitor.visitingTime = ''
          this.isSelected = true
        }else{
          this.formInline.sanyBussVisitor.visitingTime = ''
          this.isSelected = false
        }
      }
    },
  }
}
</script>
